<template>
    <div>
        <div class="main-content__title"></div>
        <div class="project-path-info">httphttp</div>
        <el-row :gutter="20" style="margin-bottom: 10px;text-align: right;">
            <el-col :span="6" :offset="18">
                <el-button size="small" @click='addRule'></el-button>
                <el-button size="small" type="primary" @click='saveFileRightNow'></el-button>
            </el-col>
        </el-row>
        <el-table border style="width: 100%" row-key="key" :stripe="true" align='center' :data="$dc.filters">
            <el-table-column prop="checked" label="" align="center" width="80">
                <template scope='scope'>
                    <el-tooltip class="item" effect="dark" content="" placement="left">
                        <el-checkbox v-model="scope.row.checked" :disabled="!$dc.filterState"></el-checkbox>
                    </el-tooltip>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template scope='scope'>
                    <rule-filter-detail :rule="scope.row"></rule-filter-detail>
                </template>
            </el-table-column>
            <el-table-column label="" :width="100" align="center" :context="_self">
                <template scope='scope'>
                    <div class="action-panel">
                        <div class="btn-panel">
                            <el-tooltip class="item" effect="dark" content="" placement="left">
                                <el-button icon='el-icon-document' size="mini"
                                           @click='onDuplicateRow(scope.row,scope.$index)'>
                                </el-button>
                            </el-tooltip>
                        </div>
                        <div class="btn-panel">
                            <el-tooltip class="item" effect="dark" content="" placement="top">
                                <el-button type="danger" icon='el-icon-delete' size="mini"
                                           @click='onDeleteRow(scope.row,scope.$index)'>
                                </el-button>
                            </el-tooltip>
                        </div>
                    </div>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import filtersApi from '../../../api/filter';
    import _ from 'lodash';
    import uuidV4  from 'uuid/v4';
    import RuleDetail from './RuleDetail';
    import Vue from 'vue';
    Vue.component(RuleDetail.name, RuleDetail);

    export default {
        name: 'filters',
        methods: {
            onDeleteRow(row, index, list) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$dc.filters.splice(index, 1);
                });
            },
            onDuplicateRow(row, index, list) {
                var copy = _.cloneDeep(this.$dc.filters[index]);
                this.$dc.filters.splice(index, 0, copy);
            },
            /**
             * 
             */
            async saveFileRightNow(){
                debugger;
                let result = await filtersApi.saveFilters(this.$dc.filters);
                if (result.code == 0) {
                    this.$message({
                        showClose: true,
                        type: 'success',
                        message: '!'
                    });
                }
            },
            addRule() {
                this.$dc.filters.unshift({
                    name: "",
                    key: uuidV4(),
                    method: "",
                    match: "",
                    checked: true,
                    actionList: [{
                        type: "addRequestHeader",// redirect  api data
                        data: {
                            target: "",// 
                            dataId: '', //id
                            modifyResponseType: '',// 
                            callbackName: "", // jsonp
                            cookieKey: "", // cookie key
                            cookieValue: "", // cookie value
                            reqHeaderKey: "", // header
                            reqHeaderValue: "",
                            resHeaderKey: "", // header
                            resHeaderValue: "",
                            queryKey: "", // query
                            queryValue: "",
                            modifyRequestScript: "", // 
                            modifyResponseScript: "" // 
                        }
                    }]
                });
            }
        }
    };

</script>
<style>
    .action-panel .btn-panel {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 10px;
    }

</style>



